<template>
  <div class="page-card-content" v-loading="loading">
    <div class="top-area">
      <el-form ref="searchForm" :model="searchForm" inline size="middle" style="width:100%">
        <div style="display:flex">
          <div style="flex:1">
            <el-form-item label="名称">
              <el-input
                v-model="searchForm.keywordName"
                placeholder="输入搜索关键字"
                clearable
                class="mr50"
              >
              </el-input>
            </el-form-item>
          </div>
          <div>
            <el-form-item>
              <el-button type="primary" @click="handleRowClick('add')">新增</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData(true)">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="resetData()">重置</el-button>
            </el-form-item>
          </div>
        </div>
      </el-form>
    </div>

    <el-table
      :data="tableData"
      stripe
      row-key="id"
      :header-cell-style="{ background: '#f7f8fb' }"
      height="620"
      @select="handlSelect"
      @select-all="handlSelect"
    >
      <template slot="empty">
        <div class="empty-img">
          <img src="@/assets/img/empty.png" />
          <p>暂无数据</p>
        </div>
      </template>
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="名称" show-overflow-tooltip prop="name"> </el-table-column>
      <el-table-column label="描述" prop="desc"> </el-table-column>
      <el-table-column label="创建人" prop="creatorName"> </el-table-column>
      <el-table-column label="创建时间" prop="createTime"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="handleRowClick('editor', scope.row)">编辑</el-button>
          <el-button type="text" @click="handleRowClick('authorization', scope.row)"
            >授权</el-button
          >
          <el-button type="text" @click="handleRowClick('delete', scope.row)" style="color:#f56c6c"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      class="main-pagination"
      :total="pageInfo.total"
      :current-page="searchForm.pageNum"
      :page-size="searchForm.pageSize"
      :page-sizes="pageInfo.pageSizes"
      @current-change="handlePageChanged"
      @size-change="handlePageSizeChanged"
      background
      layout="total, sizes, prev, pager, next"
    />
    <!-- 新增-->
    <el-dialog
      :title="operationType == 'editor' ? '编辑' : '新增'"
      width="600px"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      label-width="120px"
      style="margin-top:-20px"
    >
      <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules">
        <el-form-item label="名称" prop="name">
          <el-input v-model="addForm.name" class="labelWidth" maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="addForm.desc" class="labelWidth" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" style="float: right;">
            保存
          </el-button>
          <el-button
            type="primary"
            @click="submitForm(1)"
            plain
            style="float: right;margin-right:15px"
          >
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from '@/api/index.js'

export default {
  filters: {},
  data() {
    return {
      loading: false,
      tableData: [],
      pageInfo: {
        pageSizes: [10, 20, 50],
        total: 0
      },
      searchForm: {
        keywordName: '',
        pageNum: 1,
        pageSize: 10
      },
      dialogVisible: false,
      operationType: 'add',
      addForm: {},
      formRules: {
        name: [
          {
            trigger: 'blur',
            message: '请输入正确名称（支持中文，英文大小写）',
            required: true,
            pattern: '^[a-zA-Z\\u4e00-\\u9fa5]+$'
          }
        ]
      }
    }
  },
  created() {
    this.getTableData()
  },
  mounted() {},
  methods: {
    submitForm(type) {
      if (type == 1) {
        this.$refs.addForm.resetFields()
        this.addForm.desc = ''
        this.dialogVisible = false
      } else {
        this.$refs.addForm.validate(valid => {
          if (valid) {
            if (this.addForm.id) {
              API.getRolesMotiy(this.addForm).then(res => {
                if (res.status == 200) {
                  this.dialogVisible = false
                  this.$refs.addForm.resetFields()
                  this.$message.success('保存成功')
                  this.getTableData()
                } else {
                  this.$message.error(res.message)
                }
              })
            } else {
              API.getRolesAdd(this.addForm).then(res => {
                if (res.status == 200) {
                  this.dialogVisible = false
                  this.$refs.addForm.resetFields()
                  this.$message.success('保存成功')
                  this.getTableData()
                } else {
                  this.$message.error(res.message)
                }
              })
            }
          } else {
            this.$message.error('请检查表单内容是否填写完整')
          }
        })
      }
    },
    handlSelect(row) {
      this.clickRow = row
    },
    // 重置
    resetData() {
      this.searchForm = {
        keywordName: '',
        pageNum: 1,
        pageSize: 10
      }
      this.getTableData()
    },
    handleRowClick(type, row) {
      switch (type) {
        case 'add':
          this.dialogVisible = true
          this.operationType = 'add'
          if (this.addForm.id) {
            delete this.addForm.id
          }
          break
        case 'editor':
          // this.getDetail(row)
          this.dialogVisible = true
          this.operationType = 'editor'
          this.addForm = {
            id: row.id,
            name: row.name,
            desc: row.desc
          }
          break
        case 'delete':
          this.getAialarmDelete(row.id)
          break
        case 'authorization': //授权
          this.$router.push({
            name: 'associatedMenu',
            query: { row: JSON.stringify(row) }
          })
          break
      }
    },
    //删除
    getAialarmDelete(id) {
      const ids = []
      ids.push(id)
      this.$confirm('删除以后数据不可恢复，请谨慎操作！', '删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true
        API.getRolesDelete({ ids: ids })
          .then(res => {
            if (res.status == 200) {
              this.$message.success(res.message)
              this.getTableData()
            } else {
              this.$message.error(res.message)
            }
          })
          .finally(() => {
            this.loading = false
          })
      })
    },
    //获取详情数据
    getDetail(row) {
      API.getAialiarmDetail({ id: row.id }).then(res => {
        if (res.status == 200) {
          this.detailVisible = true
          this.detailData = res.data.rawData
          this.format()
        } else {
          this.$message.error(res.message)
        }
      })
    },
    getTableData(init) {
      this.loading = true
      if (init) {
        this.searchForm.pageNum = 1
        this.searchForm.pageSize = 10
      }
      const form = JSON.parse(JSON.stringify(this.searchForm))
      if (form.dateRange && form.dateRange.length > 0) {
        form.alarmTimeStop = form.dateRange[1]
        form.alarmTimeStart = form.dateRange[0]
      }
      delete form.dateRange

      if (!form.name) {
        delete form.name
      }
      API.getRolesList(form)
        .then(res => {
          if (res.status == 200) {
            this.pageInfo.total = res.data.total
            this.tableData = res.data.records
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
          // if (this.$route.query.pageNum) {
          //   this.$router.replace('/dataManage/datasetManage/list')
          // }
        })
    },
    handlePageChanged(value) {
      this.searchForm.pageNum = value
      this.getTableData(false)
    },
    handlePageSizeChanged(value) {
      this.searchForm.pageSize = value
      this.getTableData(false)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-table-column--selection .cell {
  padding-left: 10px;
}
#code {
  height: 500px;
  width: 500px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border: none;
}
::v-deep .el-upload-list {
  max-height: 400px;
  overflow: auto;
}
.mr50 {
  margin-right: 60px;
  width: 250px;
}
::v-deep .el-upload-dragger {
  width: 96px;
  height: 96px;
}
::v-deep .el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
::v-deep .el-upload--picture-card {
  width: 96px;
  height: 96px;
  line-height: 96px;
}

.labelWidth {
  width: 400px !important;
}
</style>
